import React, { useState } from "react";
import "./App.css";
import axios from "axios";

function App() {

  const [archivo, Setarchivo] = useState("");

  const handleOnUploadFile = e => Setarchivo({ file: e.target.files[0] });

  const handleOnSubmit = e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("file", archivo.file);
    axios
      .post("http://localhost:4000/api/files/upload", formData)
      .then(res => console.log(res.data))
      .catch(err => console.error(err));
  };

  return (
    <div className="container">
      <h1>Upload file in nodejs</h1>
      <form onSubmit={handleOnSubmit} className="w-50">
        <div className="form-group">
          <input type="file" name="file" onChange={handleOnUploadFile} />
        </div>
        <button type="submit" className="btn btn-danger">
          Submit
        </button>
      </form>
    </div>
  );

}

export default App;
